export default {
    name: 'Rotate',
    props: {
        list: {
            type: Array,
            default: () => [],
        },
    },
    created() {
        this.itemStyle()
    },
    methods: {
        itemStyle() {
            const num = this.list.length;
            console.log(num)
            this.$nextTick(() => {
                const { x, y, width, height } = this.$refs.content.getBoundingClientRect()
                console.log('x, y', x, y, this.$refs.content.getBoundingClientRect())
            })
        },
        getPosition(e) {
            console.log(e)
        },
    },
}



